@import url(https://fonts.googleapis.com/css2?family=Playball&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,600;1,300;1,400;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,600;0,700;1,400;1,600&display=swap);
/* 
//////////////////////
    custom-properties
//////////////////////
*/

:root {
  --spacer: 1rem;

  --clr--pink-100: #fcd2c0;
  --clr--pink-200: #fab89d;
  --clr-black: #000000;
  --clr-white: #ffffff;

  --ff-text: "Poppins", sans-serif;
  --ff-header: "Playball", cursive;
  --ff-title: "Playfair Display", serif;

  --fs-50: 0.75rem; /*12*/
  --fs-100: 0.875rem; /*14*/
  --fs-200: 0.937rem; /*15*/
  --fs-300: 1rem; /*16*/
  --fs-400: 1.0625rem; /*17*/
  --fs-500: 1.1875rem; /*19*/
  --fs-600: 1.375rem; /*22*/
  --fs-700: 1.75rem; /*28*/
  --fs-750: 1.875rem; /*30*/
  --fs-800: 1.75rem; /*43*/
  --fs-900: 3.75rem; /*57*/
  --fs-950: 3.75rem; /*60*/

  --fw-light: 300;
  --fw-regular: 400;
  --fw-med: 500;
  --fw-semi-bold: 600;
  --fw-bold: 700;

  --m-size: 970px;
}

h1,
h2,
h3,
h4,
h5 {
  line-height: 1.2;
}

ul[class],
li[class] {
  list-style: none;
  padding: 0;
}

a {
  color: black;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}
body {
  font-size: 1.0625rem;
  font-size: var(--fs-400);
  font-family: "Poppins", sans-serif;
  font-family: var(--ff-text);
  font-weight: 300;
  font-weight: var(--fw-light);
}

/*  
/////////////////
    resets
/////////////////
*/
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  display: block;
}

i {
  font-size: 1.5rem;
}

/* 
//////////////////////
    utility classes
//////////////////////
*/

.flow-content > * + * {
  margin-top: 1rem;
  margin-top: var(--spacer);
}

.flow-content-xl {
  --spacer: 1.5rem;
}

.fancy-border {
  position: relative;
}

.fancy-border::after {
  content: "";
  width: 1.8rem;
  height: 3px;
  position: absolute;
  top: -10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);

  background-color: var(--clr-black);

  background-color: var(--fancy-border, var(--clr-black));
}

/* 
//////////////////////
    general styling
//////////////////////
*/

.toggle-icon {
  display: none;
  cursor: pointer;
}

/* 
//////////////////////
    components
//////////////////////
*/

.unobserve {
  opacity: 0;
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  transition: opacity ease-in-out 1s, -webkit-transform ease-in-out 1s;
  transition: opacity ease-in-out 1s, transform ease-in-out 1s;
  transition: opacity ease-in-out 1s, transform ease-in-out 1s, -webkit-transform ease-in-out 1s;
}

.observe {
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  transition: opacity ease-in-out 1s, -webkit-transform ease-in-out 1s;
  transition: opacity ease-in-out 1s, transform ease-in-out 1s;
  transition: opacity ease-in-out 1s, transform ease-in-out 1s, -webkit-transform ease-in-out 1s;
}

/* 
///////////////
$    logo
///////////////// 
*/
.logo {
  font-size: var(--fs-750);
  font-family: var(--ff-title);
  font-weight: var(--fw-semi-bold);
  margin-right: 2rem;
}

.logo sup {
  font-size: var(--fs-700);
  position: relative;
}

.logo sup::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 3px;
  right: 3px;
  border-bottom: 2px solid black;
}

/* 
///////////////
$    navbar
///////////////// 
*/
.nav-container {
  padding: 0.9rem 0;
  text-transform: uppercase;
}

.nav__inner {
  max-width: 85rem;

  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
  padding: 0 2rem;
}

.nav-items {
  display: flex;
  align-items: center;
}

.nav-list {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;

  --spacer: 1.5rem;
  margin: 0 1.5rem;
  font-size: var(--fs-100);
}

/* 
///////////////
$   nav-social 
///////////////
 */
.nav-social-list {
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.nav-social-list i {
  transition: all ease-in 150ms;
}

.fa-instagram-square:hover {
  color: #e4372b;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.fa-twitter-square:hover {
  color: #00acee;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}
.fa-facebook:hover {
  color: #4267b2;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.login {
  margin: 0 1rem;
  font-size: var(--fs-400);
  text-transform: capitalize;

  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

/* 
//////////////////
$       media for navbar
////////////////////
 */
@media (max-width: 970px) {
  .logo {
    position: absolute;
    top: 15px;
    left: 15px;
  }

  .nav-container {
    padding: 0;
  }

  .nav__inner {
    position: relative;
    z-index: 10;
    margin-bottom: 5rem;
  }

  .nav-items {
    width: 60%;
    height: 100vh;
    padding-top: 4rem;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    grid-gap: 1rem;
    gap: 1rem;

    position: fixed;
    top: 0;
    right: -100%;

    background-color: var(--clr-white);
    transition: all ease 500ms;
    box-shadow: -10px 0 10px rgba(0, 0, 0, 0.2);
  }

  .nav-items-deactive {
    right: 0;
  }

  .nav-list {
    width: 100%;
    margin: 0;

    font-size: var(--fs-500);
    font-family: var(--ff-title);
    font-weight: var(--fw-med);

    display: flex;
    flex-direction: column;
  }

  .nav-list li {
    flex-grow: 1;
    padding-left: 2rem;
  }

  .social-container {
    width: 100%;
    display: flex;
    padding-left: 3rem;
  }

  .nav-social-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    grid-gap: 2rem;
    gap: 2rem;
  }

  .toggle-icon {
    display: block;
    position: absolute;
    top: 30px;
    right: 30px;
  }

  .login {
    flex-direction: row;
    position: absolute;
    top: 15px;
    left: 0;
  }
}

/* 
//////////////////////
$    footer
//////////////////////
*/

.footer-container {
  background-color: var(--clr-black);
  padding: 4rem 2rem;
  color: var(--clr-white);
  text-align: center;
  font-family: var(--ff-title);
}

.creator span {
  font-style: italic;
  font-size: var(--fs-600);
}

.footer-social-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 1rem;
  grid-gap: 1rem;
  gap: 1rem;
}

.footer-social-list i {
  font-size: 2.5rem;
  border-radius: 50%;

  transition: -webkit-transform ease-in-out 0.2s;

  transition: transform ease-in-out 0.2s;

  transition: transform ease-in-out 0.2s, -webkit-transform ease-in-out 0.2s;
}

.our-services-container {
  background-color: pink;
  padding: 6rem 1rem;
}

.our-services-title {
  font-size: var(--fs-950);
  font-weight: var(--fw-regular);
  font-family: var(--ff-header);
  text-align: center;

  margin-bottom: 2rem;
}

.our-services {
  max-width: 60rem;
  background-color: var(--clr-white);
  margin: 0 auto;
  padding: 3rem 1rem 1rem;
  min-height: 600px;
}

.headers {
  max-width: -webkit-max-content;
  max-width: max-content;
  margin: 0 auto 3rem;

  display: flex;
  flex-direction: column;
}

.selected-item {
  background-color: var(--clr--pink-200);
  width: 54px;
  height: 4px;
  -webkit-transform: translate(0, -3px);
          transform: translate(0, -3px);

  transition: -webkit-transform 0.3s ease-in-out;

  transition: transform 0.3s ease-in-out;

  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.headers .headers-list {
  display: flex;
  justify-content: space-around;
  grid-gap: 3rem;
  gap: 3rem;
  padding-bottom: 0.3rem;

  font-family: var(--ff-title);
  text-transform: uppercase;
  font-weight: var(--fw-med);
  background: white;
  border: none;
  font-size: var(--fs-500);
  cursor: pointer;

  border-bottom: 2px solid rgb(199, 197, 197);
}

.services-section {
  border-top: 2px solid rgb(102, 101, 101);
  padding: 2rem 0;
}

.services-section:last-child {
  border-bottom: 2px solid rgb(102, 101, 101);
}

.single-service {
  display: flex;
  grid-gap: 3.5rem;
  gap: 3.5rem;
  align-items: flex-start;
}

.single-service > * {
  flex-grow: 0;
}

.single-service-desc {
  flex-basis: 50%;
}

.single-service-desc h2 {
  font-size: var(--fs-600);
  font-family: var(--ff-title);
  text-transform: capitalize;
  margin-bottom: 0.7rem;
}

.single-service-desc p {
  font-size: var(--fs-100);
  line-height: 1.5;
}

.single-hour-price {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  font-size: var(--fs-100);
}

.single-service-btn {
  margin-left: auto;
  padding: 0.3rem 1rem;

  background-color: var(--clr--pink-200);
  color: var(--clr-white);
  text-transform: capitalize;
  font-size: var(--fs-100);
}

/* mobile view of the selection */
.select-services {
  display: none;

  border-radius: 0;
  width: 100%;
  min-height: 2.5rem;
  font-family: var(--ff-text);
  text-transform: uppercase;
  margin-bottom: 3rem;
}

.select-services {
  -webkit-appearance: none;
  appearance: none;
  background-color: var(--clr--pink-100);
  border: none;
  border-radius: 3px;
  padding: 0 1rem;
}

select::-ms-expand {
  display: none;
}

.select {
  position: relative;
  display: none;
  transition: all ease 2s;
}

select option {
  line-height: 200px;
}
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: var(--clr--pink-200);
  padding: 0.5rem 1.3rem;
  pointer-events: none;
}

.lds-dual-ring {
  width: 100px;
  height: 100px;
  margin: 150px auto;
}
.lds-dual-ring::after {
  content: " ";
  display: block;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 6px solid rgb(255, 194, 204);
  border-color: rgb(255, 194, 204) transparent rgb(255, 194, 204) transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media (max-width: 600px) {
  .our-services .headers {
    display: none;
  }

  .our-services-title {
    font-size: 2.5rem;
  }

  .select {
    display: block;
  }

  .single-service {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .single-service-btn {
    font-size: var(--fs-20);
    padding: 1rem 0.5rem;
    margin: 0;
    width: 80%;
    text-align: center;
  }

  .single-service-desc h2 {
    font-size: var(--fs-600);
    text-align: center;
  }
  .single-service-desc p {
    font-size: var(--fs-100);
    text-align: center;
  }

  .single-hour-price {
    text-align: center;
  }

  .select-services {
    display: block;
    font-size: var(--fs-500);
  }
}

.calendar-container {
  padding: 1rem 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.calendar-link {
  margin-bottom: 3rem;
  display: inline-block;
  color: var(--clr--pink-200);
  background: none;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
}

.calendar-wrapper {
  display: flex;
  flex-direction: row;
  grid-gap: 2rem;
  gap: 2rem;
  align-items: flex-start;
}

.calendar-content {
  display: flex;
  flex-direction: column;
}

.calendar-title {
  font-family: var(--ff-title);
  font-weight: var(--fw-regular);
  margin-bottom: 4rem;
}

.calendar {
  display: flex;
  flex-direction: column;
}

.calendar .time-zone {
  font-size: 0.8rem;
  font-weight: var(--fw-semi-bold);
  margin-bottom: 1rem;
}

.time-zone .local-time-zone {
  color: var(--clr--pink-200);
  text-decoration: underline;
  margin-left: 0.5rem;
}

.calendar-btn {
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  align-items: baseline;
  margin-bottom: 2rem;
}

.calendar-btn .time {
  font-size: var(--fs-100);
  display: flex;
  grid-gap: 0.2rem;
  gap: 0.2rem;
  align-items: center;
}
.calendar-btn .time > * {
  width: 35px;
}

/* time navigator btns */

.back-forward-btn button,
.today-btn {
  padding: 0.15rem 0.4rem;
  background-color: white;
  border: 1px solid black;
}

.back-forward-btn .back-time {
  border-right: none;
}

.back-forward-btn button:hover,
.today-btn:hover {
  cursor: pointer;
}

/*$ floating box beside the calendar */
.calendar-floating {
  border: 1px solid rgb(146, 146, 146);
  padding: 1.5rem 1rem;
  position: -webkit-sticky;
  position: sticky;
  top: 100px;
  min-width: 250px;
}

.calendar-floating .floating-service {
  font-size: var(--fs-400);
  font-weight: var(--fw-regular);
  flex-grow: 0;
}

.calendar-floating .floating-time {
  font-size: var(--fs-50);
  font-weight: var(--fw-regular);
  margin: 1rem 0;
}

.calendar-floating-btn {
  width: 100%;
  display: inline-block;
  padding: 0.5rem 1rem;

  background-color: var(--clr--pink-200);
  color: white;
  text-align: center;
  font-size: var(--fs-100);
  border: none;
  cursor: pointer;
}

.calendar-floating hr {
  margin-bottom: 1.5rem;
}

.floating-date {
  margin-bottom: 1rem;
  font-size: 0.89rem;
  font-weight: var(--fw-semi-bold);
  text-align: center;
  text-transform: capitalize;
}

.floating-date span:last-child {
  padding: 0 0.7rem;
}

.floating-date span {
  padding: 0 0.05rem;
}

.calendar-error {
  color: red;
  font-size: var(--fs-100);
  font-weight: var(--fw-semi-bold);
  text-align: center;
  margin: 1rem auto 0;
}

/*$ calendar elements */

.calendar-table,
td {
  border-collapse: collapse;
}

td {
  border: 1px solid gray;
  padding: 1rem 3rem 2rem 1rem;
  transition: outline 50ms ease-in;
  cursor: pointer;
  width: 1rem;
  height: 1rem;
  color: black;
  font-weight: bold;
}

th {
  padding-bottom: 0.7rem;
}

td:hover {
  outline: 2px solid var(--clr--pink-200);
  outline-offset: -2px;
}

.today {
  text-decoration: underline;
  color: var(--clr--pink-200);
}

.prev-date,
.next-date {
  color: gray;
}

.unavailable p {
  text-align: center;
  font-size: var(--fs-500);
  font-weight: bold;
  margin: 2rem 0;
  color: red;
}

.not-available {
  color: gray;
}

.active {
  display: block;
}

.inactive {
  display: none;
}

.toggle-weekly,
.toggle-monthly {
  margin-top: 1rem;
  display: inline-block;
  background-color: white;
  border: none;
  color: var(--clr--pink-200);
}

.calendar-btn-mobile {
  display: none;
}

.time-order-view {
  display: none;
}

.time-zone-mobile-view {
  display: none;
}

.submit-info {
  display: none;
}

/* 
/////////////////////////
responsive calendar view
/////////////////////////
*/

@media (max-width: 900px) {
  .calendar-btn,
  .calendar-floating,
  .time-zone {
    display: none;
  }

  .calendar {
    max-width: 500px;
  }

  /* month navigator */
  .calendar-btn-mobile {
    padding: 1rem 1rem;
    display: block;
  }

  .time-mobile {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .back-time,
  .forward-time {
    color: var(--clr--pink-200);
    opacity: 0.7;
    background: none;
    border: none;
    font-size: 2.3rem;
    cursor: pointer;
  }

  .month-year {
    font-weight: bold;
    text-transform: capitalize;
  }

  /* calendar table */

  .calendar-table-container {
    margin: 0 auto;
    padding: 0 1rem;
  }
  .calendar-table {
    margin: 0 auto;
  }

  tbody {
    border-top: 2px solid rgba(185, 184, 184, 0.712);
    border-bottom: 2px solid rgb(185, 184, 184, 0.712);
  }
  td {
    border: none;
    padding: 0.8rem 0.9rem;
    font-weight: normal;
    text-align: center;
    font-size: 0.8rem;
  }
  td:hover,
  td:focus {
    outline: none;
    color: white;
    background-color: var(--clr--pink-200);
    border-radius: 50%;
    text-align: center;
  }

  th {
    font-weight: bold;
    font-size: 0.8rem;
  }

  .today {
    color: var(--clr--pink-200);
  }

  /* time zone select */

  .time-zone-mobile-view {
    display: block;
    margin: 2rem 1rem;
  }
  .time-zone-mobile-view p {
    margin-bottom: 0.3rem;
    font-weight: bold;
    font-size: 0.9rem;
    text-transform: capitalize;
  }

  .time-zone-mobile-view .local-time-zone {
    color: var(--clr--pink-200);
    text-decoration: underline;
  }

  .time-order-view {
    display: block;
    width: -webkit-max-content;
    width: max-content;
    margin: 0 auto;
    padding: 0.1rem 0;
    font-size: 0.7rem;

    background: none;
    border: none;
    border-bottom: 1px solid black;

    cursor: pointer;
  }

  /* calendar mobile view port btn */
  .submit-info {
    display: block;

    background-color: var(--clr--pink-200);
    color: white;
    font-weight: bold;
    font-size: var(--fs-500);
    border: none;

    text-align: center;
    margin: 1.5rem 2rem 0.7rem;
    padding: 0.9rem 0;

    cursor: pointer;
  }
}

.order-time-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
  grid-gap: 1rem;
  gap: 1rem;

  width: 100%;
  margin: 2rem 0;
}

.order-time-wrapper > * {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.order-time-wrapper span {
  border: solid 1px gray;
  padding: 0.2rem 2rem;
  transition: outline 80ms ease;
  cursor: pointer;
}

.order-time-wrapper span:hover {
  outline: solid 3px var(--clr--pink-200);
  outline-offset: -2px;
}

.order-time-wrapper .order-title {
  font-weight: bold;
  font-size: 1.2rem;
  text-align: center;
}

.order-time-wrapper span.active {
  color: var(--clr--pink-200);
  outline: solid 2px var(--clr--pink-200);
  outline-offset: -2px;
}

@media (max-width: 900px) {
  .order-title {
    display: none;
  }

  .order-time-wrapper {
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    padding: 0 0.5rem;
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }

  .order-time-wrapper > * {
    width: 100%;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }

  .order-time-wrapper span {
    flex-basis: 30%;
    padding: 0.3rem 0.5rem;
    font-size: 0.7rem;
    text-align: center;
  }

  .inactiveTime {
    display: none;
  }

  .activeTime {
    display: flex;
    justify-content: center;
  }
}


/* 
/////////////
$ coupon
//////////////
 */
 .coupon-form{
    width: 100%;
    position: relative;
    
