  }
  
  #tree{
    stroke: #59513C;
  }
  
  #wood-stump{
    stroke: #59513C;
    -webkit-animation: wood-stump 3s infinite ease-in-out;
    animation: wood-stump 3s infinite ease-in-out;
  }
  
  @-webkit-keyframes wood-stump{ 0% { -webkit-transform: translate(100px)  } 50% { -webkit-transform:  translate(50px); } 100% { -webkit-transform:  translate(100px); } }
  @keyframes wood-stump{ 0% {-webkit-transform:  translate(100px);transform:  translate(100px);  } 50% {-webkit-transform:  translate(0px);transform:  translate(0px); } 100% {-webkit-transform:  translate(100px);transform:  translate(100px); } }
  
  
  #leaf{
    stroke: #59513C;
    -webkit-animation: leaf 7s infinite ease-in-out;
    animation: leaf 7s infinite ease-in-out;
  }
  
  @-webkit-keyframes leaf{ 0% { -webkit-transform: translate(0, 70px)  } 50% { -webkit-transform:  translate(0, 50px); } 100% { -webkit-transform:  translate(0, 70px); } }
  @keyframes leaf{ 0% {-webkit-transform:  translate(0, 70px);transform:  translate(0, 70px);  } 50% {-webkit-transform:  translate(0px);transform:  translate(0px); } 100% {-webkit-transform:  translate(0, 70px);transform:  translate(0, 70px); } }
  
  #border{
    stroke: #59513C;
  }
  
  #Page{
   fill: #59513C;
  }
  #notFound{
    fill: #A7444B;
  }
  
